<!--  -->
<template>
	<div class="right-content site-info">
		<el-tabs v-model="activeName" @tab-click="handleClick" class="content">
		    <el-tab-pane label="" name="first">
		    <!--  -->
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="siteName" placeholder=""></el-input>
					<span class="required">*</span>
				</div>
				<div class="site-info-content">
					<span class="site-title">ICP</span>
					<el-input v-model="siteIcp"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="siteGov"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="siteAdv"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title">seo</span>
					<el-input v-model="siteSeoTitle"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title">seo</span>
					<el-input v-model="siteSeoKeywords"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title" style="vertical-align: top;">seo</span>
					<el-input v-model="siteSeoDescription" type="textarea" autosize></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="siteAdminEmail"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="siteAnalytics"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="hospitalPhone"></el-input>
				</div>
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-input v-model="hospitalAddress"></el-input>
				</div>
		    </el-tab-pane>

		    <el-tab-pane label="" name="second">
	    	<!--  -->
				<div class="site-info-content"><el-checkbox v-model="openComment"></el-checkbox></div>
  				<div class="site-info-content"><el-checkbox v-model="commentNeedCheck"></el-checkbox></div>
  				<div class="site-info-content"><el-checkbox v-model="commentAllowAnonymous"></el-checkbox></div>
  				<div class="site-info-content comment-time"><el-input v-model="commentTimeInterval" placeholder="" type="number"></el-input></div>
		    </el-tab-pane>

			<el-tab-pane label="" name="third">
			<!--  -->
				<div class="site-info-content">
					<span class="site-title"></span>
					<el-select v-model="openRegistration" placeholder="">
		    			<el-option v-for="item in option" :key="item.value" :label="item.label" :value="item.value"></el-option>
		  			</el-select>
				</div>
				<div class="site-info-content">
					<span class="site-title" style="vertical-align: top;"></span>
					<el-input v-model="bannedUsernames" type="textarea" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
				</div>
			</el-tab-pane>
	  	</el-tabs>
	  	<div class="submit-btn"><el-button type="primary" @click="saveSiteInfo()"></el-button></div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		data() {
			return {
				activeName: 'first',
				siteName: '',
				siteIcp: '',
				siteGov: '',
				siteAdv: '',
				siteSeoTitle: '',
				siteSeoKeywords: '',
				siteSeoDescription: '',
				siteAdminEmail: '',
				siteAnalytics: '',
				canSave: false,
				openComment: true,
				commentNeedCheck: true,
				commentAllowAnonymous: true,
				commentTimeInterval: '',
				option: [{value: 1, label: ''},{value: 0, label: ''}],
				openRegistration: '',
				bannedUsernames: '',
				hospitalPhone: '',
				hospitalAddress: '',
			}
		},
		methods: {
			handleClick() {
				
			},
			saveSiteInfo() {
				var self = this;
				if(this.activeName == 'first') {
					this.siteName == '' ? this.$alert('', {confirmButtonText: ''}) : this.canSave = true;

					if(this.canSave) {
						apis.sitePost({site_name: this.siteName, site_icp: this.siteIcp, site_seo_title: this.siteSeoTitle, site_seo_keywords: this.siteSeoKeywords, site_seo_description: this.siteSeoDescription, site_admin_email: this.siteAdminEmail, site_analytics: this.siteAnalytics, site_telephone: this.hospitalPhone, site_address: this.hospitalAddress, site_adv: this.siteAdv, site_gov: this.siteGov, token: this.$root.token}, function(bck) {
							alertMsg(bck.code, bck.info);
						}.bind(this))
					}
				}
				else if(this.activeName == 'second') {
					apis.commentPost({open_comment: this.openComment, comment_need_check: this.commentNeedCheck, comment_allow_anonymous: this.commentAllowAnonymous, comment_time_interval: this.commentTimeInterval, token: this.$root.token}, function(bck) {
						alertMsg(bck.code, bck.info);
					}.bind(this))
				}
				else if(this.activeName == 'third') {
					apis.userPost({open_registration: this.openRegistration, banned_usernames: this.bannedUsernames, token: this.$root.token}, function(bck) {
						alertMsg(bck.code, bck.info);
					}.bind(this))
				}
				function alertMsg(code, info) {
					info == '' ? info = '' : '';
					self.$alert(info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			          		if(info == '') {
			            		location.reload();
			            	}
			          	}
			        });
				}
			}
		},
		created() {
			apis.site({name: 'site_info', token: this.$root.token}, function(bck) {
				if(bck.code == 0) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.siteName = bck.site_name;
					this.siteIcp = bck.site_icp;
					this.siteGov = bck.site_gov;
					this.siteAdv = bck.site_adv;
					this.siteSeoTitle = bck.site_seo_title;
					this.siteSeoKeywords = bck.site_seo_keywords;
					this.siteSeoDescription = bck.site_seo_description;
					this.siteAdminEmail = bck.site_admin_email;
					this.siteAnalytics = bck.site_analytics;
					this.hospitalPhone = bck.site_telephone;
					this.hospitalAddress = bck.site_address;
				}
			}.bind(this))

			apis.site({name: 'comment_info', token: this.$root.token}, function(bck) {
				this.openComment = bck.open_comment;
				this.commentNeedCheck = bck.comment_need_check;
				this.commentAllowAnonymous = bck.comment_allow_anonymous;
				this.commentTimeInterval = bck.comment_time_interval;
			}.bind(this))

			apis.site({name: 'user_info', token: this.$root.token}, function(bck) {
				this.openRegistration = bck.open_registration;
				this.bannedUsernames = bck.banned_usernames;
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.site-info {
		.site-info-content {
			font-size: 15px;
			padding-left: 5%;
			margin-top: 15px;
			position: relative;
			.site-title {
				width: 100px;
				margin-right: 15px;
				text-align: right;
				display: inline-block;
			}
			.el-input {
				width: auto;
			}
			.el-textarea {
				width: 202px;
			}
			.el-select {
				input {
					width: 202px;
				}
			}
		}
		.submit-btn {
			padding: 15px 0 0 7%;
		}
		.comment-time {
			.el-input {
				width: 55px;
				margin: 0 10px;
			}
		}
	}
</style>


