+<!--  -->
<template>
	<div class="right-content">
  		<el-tabs v-model="activeName" @tab-click="changeTab" :before-leave='leaveTab' class="content web-nav">

		    <el-tab-pane label="" name="first">

		    	<!--  -->
				<el-select v-model="navListId" @change="webNavSelect" class="web-nav-select">
				    <el-option v-for="item in navClassList" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>

				<!--  -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order"></el-button>

				<el-table :data="webNavList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="listOrder[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="50"></el-table-column>
      				<el-table-column label="">
      					<template slot-scope="scope">
      						<div v-if="webNavList[scope.$index]._level == 1">{{webNavList[scope.$index].label}}</div>
      						<div v-else-if="webNavList[scope.$index]._level == 2"><span style="margin: 0 15px 0 30px;"></span>{{webNavList[scope.$index].label}}</div>
      						<div v-else-if="webNavList[scope.$index]._level == 3"><span style="margin: 0 15px 0 60px;"></span>{{webNavList[scope.$index].label}}</div>
      						<div v-else-if="webNavList[scope.$index]._level == 4"><span style="margin: 0 15px 0 90px;"></span>{{webNavList[scope.$index].label}}</div>
      					</template>
      				</el-table-column>
      				<el-table-column label="" prop="href" width="270"></el-table-column>
      				<el-table-column label="" width="80">
	    				<template slot-scope="scope">
	    					<div>{{webNavList[scope.$index].status == 1 ? '' : ''}}</div>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="" width="200">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="addChildrenNav(scope.row)"></el-button>
	        				<el-button type="text" size="small" @click="goToEdit(scope.row)"></el-button>
	    					<el-button type="text" size="small" @click="deleteNav(scope.row)"></el-button>
	      				</template>
	    			</el-table-column>

    			</el-table>
		    </el-tab-pane>
			
		    <el-tab-pane label="" name="second">
		    	<!--  -->
		    	<!-- v-ifreset -->
				<webNavEditAndAdd v-if="navListAdd" v-on:change = "activeChange($event)"></webNavEditAndAdd>
		    </el-tab-pane>

		    <el-tab-pane label="" name="third" v-if="navListEdit">
		    	<!--  -->
				<webNavEditAndAdd v-on:change = "activeChange($event)"></webNavEditAndAdd>
		    </el-tab-pane>
			
			<!--  -->
		  	<el-dialog title="" :visible.sync="dialogWebNavVisible" class="navDialog" width="40%" @close='closeDialog'>
		  		<div style="padding-left: 10%;">
		  			<p><span class="title"></span><b>{{webNavParent}}</b></p>
					<p>
						<span class="title"></span>
						<el-input placeholder="" v-model="navName" clearable></el-input>
		    			<span class="required">*</span>
					</p>
					<p>
						<span class="title"></span>
						<el-input placeholder="" v-model="navHref" clearable></el-input>
					</p>
					<p>
						<span class="title"></span>
						<el-select v-model="navStatu" placeholder="">
			    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
			  			</el-select>
		    			<span class="required">*</span>
					</p>
		  		</div>
				<span slot="footer" class="dialog-footer">
			    	<el-button @click="closeDialog()"> </el-button>
				    <el-button type="primary" @click="submitNavChildrenAdd()"> </el-button>
				</span>
			</el-dialog>
			
			<el-tab-pane label="" name="fourth">
				<el-table :data="navClassList" style="width: 100%" :border='true'>
	    			<el-table-column label="ID" prop="id" width="50"></el-table-column>
      				<el-table-column label="" prop="name"></el-table-column>
      				<el-table-column label="" prop="remark" width="250"></el-table-column>
	    			<el-table-column label="" width="130">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="goToEditNavClass(scope.row)"></el-button>
	    					<el-button type="text" size="small" @click="deleteNavClass(scope.row)"></el-button>
	      				</template>
	    			</el-table-column>
    			</el-table>
		    </el-tab-pane>

		    <el-tab-pane label="" name="firth">
				<div class="nav-class-name">
					<span class="title"></span>
					<el-input v-model="addNavClassName" placeholder=""></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="nav-class-remark">
					<span style="vertical-align: top;" class="title"></span>
					<el-input type="textarea" :rows="3" v-model="addNavClassRemark" clearable></el-input>
				</div>
				<div class="nav-class-btn">
		    		<el-button type="primary" @click="submitNavAdd()"></el-button>
		    		<el-button @click="goBack()"></el-button>
		    	</div>
		    </el-tab-pane>

		    <!--  -->
		  	<el-dialog title="" :visible.sync="dialogNavClassVisible" class="navDialog" width="40%">
		  		<div style="padding-left: 10%;">
		  			<p>
		  				<span class="title"></span>
		  				<el-input v-model="editNavClassName" clearable></el-input>
		    			<span class="required">*</span>
		  			</p>
					<p>
						<span style="vertical-align: top;" class="title"></span>
						<el-input type="textarea" :rows="3" v-model="editNavClassRemark" clearable></el-input>
					</p>
		  		</div>
		  		<span slot="footer" class="dialog-footer">
			    	<el-button @click="closeNavClassDialog()"> </el-button>
				    <el-button type="primary" @click="submitNavClassEdit()"> </el-button>
				</span>
			</el-dialog>
  		</el-tabs>
  	</div>
</template>

<script>
	import apis from '../../apis'
	import webNavEditAndAdd from '@/views/web-nav/web-nav-EditAndAdd'

	export default {
		inject: ['reload'],
		components: {
			webNavEditAndAdd
		},
		data() {
			return {
				activeName: 'first',
				navClassList: [],
				navListId: '',
				webNavList: [],
        		listOrder: [],
        		navId: [],
        		loading: true,
        		navListAdd: false,
        		navListEdit: false,
        		dialogWebNavVisible: false,
        		dialogNavClassVisible: false,
        		webNavParent: '',
        		navName: '',
        		navHref: '',
        		navStatu: '',
        		navParentId: '',
                status: [{value: 1, label: ''},{value: 0, label: ''}],
                updateNavListOrderArr: [],
                navClassId: '',
                editNavClassName: '',
                editNavClassRemark: '',
                cloneListOrder: [],
                originalListOrderArr: [],
                addNavClassName: '',
                addNavClassRemark: ''
			}
		},
		methods: {
			leaveTab(activeName, oldActiveName){
				
			},
			//
			changeTab(tab, event) {
				this.$root.navClassList = this.navClassList;
				this.navListAdd = true;

				tab.index == '1' ? (this.$root.propMenuType = 'add') | (this.$root.navClassList = this.navClassList) | (this.$root.catId = this.navListId) : this.navListAdd = false;
				tab.index != '3' ? (this.addNavClassName = '') | (this.addNavClassRemark = '') : '';
				tab.index != '4' ? this.navListEdit = false : '';
			},
			// 
			webNavSelect() {
				this.loading = true;
				apis.navClassInx({id: this.navListId, token: this.$root.token}, function(data) {
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.webNavList = data;
						data ? this.loading = false : '';
						this.listOrder=[];
						this.navId=[];
					for(var i = 0; i < data.length; i ++) {
						this.navId.push(data[i].id);
	    				this.listOrder.push(data[i].list_order);
	    		}
						
					}
				}.bind(this))
			},
			// web-nav-edit 
		    activeChange(name) {
		    	this.activeName = name[0];
		    	this.navListAdd = false;
		    	this.navListEdit = false;
		    	// 1
		    	name[1] == 1 ? this.reload() : '';
		    },
		    // 
		    deleteNav(row) {
		    	this.$confirm(''+ row.label +'', '', {
		          	distinguishCancelAndClose: true,
		          	confirmButtonText: '',
		          	cancelButtonText: ''
		        }).then(() => {
		            // 
		            apis.webNavDelete({id: row.id, token: this.$root.token}, function(bck) {
			    		// methods
		    			this.$options.methods.alertMsg(this, bck.info, bck.code);
			    	}.bind(this))
	          	}).catch(action => {});
		    },
		    // 
		    addChildrenNav(row) {
		    	this.dialogWebNavVisible = true;
		    	this.webNavParent = row.label;
		    	this.navParentId = row.id;
		    },
		    // reset
		    closeDialog(){
		      	this.navName = '';
        		this.navHref = '';
        		this.navStatu = '';
        		this.navHref = '';
        		this.dialogWebNavVisible = false;
		    },
		    // 
		    submitNavChildrenAdd() {
		    	this.navName == '' ? this.$alert('', {confirmButtonText: ''}) : this.navStatu === '' ? this.$alert('', {confirmButtonText: ''}) : this.canSubmit = true;
                this.navHref == '' ? this.navHref = '/' : '';

		    	if(this.canSubmit) {
		    		apis.webNavAdd({cat_id: this.navListId, parent_id: this.navParentId, href: this.navHref, name: this.navName, status: this.navStatu, target: '', token: this.$root.token}, function(bck) {
			    		// methods
			    		this.$options.methods.alertMsg(this, bck.info, bck.code);
			    	}.bind(this))
		    	}
		    },
		    // 
		    goToEdit(row) {
				this.$root.navClassList = this.navClassList;
		    	this.$root.propMenuType = 'edit';
		    	this.$root.navEdit = row;
		    	this.navListEdit = true;
		    	this.activeName = 'third';
		    },
		    // 
		    changeListOrder() {
	    		for(var i = 0; i < this.navId.length; i++) {
					this.updateNavListOrderArr.push({id: JSON.parse(JSON.stringify(this.navId))[i], list_order: JSON.parse(JSON.stringify(this.listOrder))[i]})
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(this.navId))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]})
				}
				
				apis.webNavListOrder({list_order: this.updateNavListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
					// methods
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
		    },
		    // 
		    goToEditNavClass(row) {
		    	this.dialogNavClassVisible = true;
		    	this.navClassId = row.id;
		    	this.editNavClassName = row.name;
		    	this.editNavClassRemark = row.remark;
		    },
		    // 
		    deleteNavClass(row) {
		    	apis.navClassDelete({id: row.id, token: this.$root.token}, function(bck) {
		    		if(bck.code == 1) {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				            	// 
				            	apis.navClass({token: this.$root.token}, function(data) {this.navClassList = data;}.bind(this));
				          	}
				        });
		    		}
		    		else {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				          		if(bck.info == ''){
				          			location.reload();
				          		}
				          	}
				        });
		    		}
		    	}.bind(this))
		    },
		    // 
		    closeNavClassDialog() {
		    	this.dialogNavClassVisible = false;
		    },
		    // 
		    submitNavClassEdit() {
		    	apis.navClassEdit({id: this.navClassId, name: this.editNavClassName, remark: this.editNavClassRemark, token: this.$root.token}, function(bck) {
		    		if(bck.code == 1) {
		    			this.$alert('', {
			         	 	confirmButtonText: '',
				          	callback: action => {
				            	this.dialogNavClassVisible = false;
				            	// 
				            	apis.navClass({token: this.$root.token}, function(data) {this.navClassList = data;}.bind(this));
				          	}
				        });
		    		}
		    		else {
		    			if(bck.info == '') {
		    				this.$alert(bck.info, {
				         	 	confirmButtonText: '',
					          	callback: action => {
				          			location.reload();
					          	}
					        });
		    			}
		    			else {
		    				this.$alert('', {confirmButtonText: ''});
		    			}
		    		}
		    	}.bind(this))
		    },
		    // 
		    submitNavAdd() {
		    	apis.navClassAdd({name: this.addNavClassName, remark: this.addNavClassRemark, token: this.$root.token}, function(bck) {
		    		if(bck.code == 1) {
		    			this.$alert(bck.info, {
                            confirmButtonText: '',
                            callback: action => {
                                this.activeName = 'fourth';
				            	// 
				            	apis.navClass({token: this.$root.token}, function(data) {
				            		this.navClassList = data;}.bind(this));
                            }
                        });
		    		}
		    		else {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				          		if(bck.info == ''){
				          			location.reload();
				          		}
				          	}
				        });
		    		}
		    	}.bind(this))
		    },
		    // 
            goBack() {
                this.addNavClassName = '';
                this.addNavClassRemark = '';
                this.activeName = 'fourth';
            },
		    alertMsg: function(_this, msg, code) {
		    	_this.$alert(msg, {
	         	 	confirmButtonText: '',
		          	callback: action => {
		            	if(msg == ''){
		          			location.reload();
		          		}
		          		else if(code == 1) {
		          			_this.reload();
		          		}
		          	}
		        });
		    }
		},
		created() {
			apis.navClass({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.navClassList = data;
					this.navListId = data[0].id;
				}
			}.bind(this))

			// 
			apis.navClassInx({id: 1, token: this.$root.token}, function(data) {
				this.webNavList = data;

				// idlist-order
				for(var i = 0; i < data.length; i ++) {
	    			this.navId.push(data[i].id);
	    			this.listOrder.push(data[i].list_order);
	    		}
	    		Object.assign(this.cloneListOrder, this.listOrder);

			}.bind(this))

		}
	}
</script>

<style lang="less">
		.web-nav {
			.change-list-order {
				margin-left: 15px;
			}
			.web-nav-select {
			    margin-bottom: 15px;
			}
			.el-table__expanded-cell {
				padding: 0;
			}
			.web-nav-children {
				.el-table__header-wrapper {
					display: none;
				}
			}
			.navDialog {
				.el-input {
					width: auto;
				}
				.el-textarea {
					width: 217px;
				}
			}
			.nav-class-name, .nav-class-remark {
        		font-size: 15px;
		        padding-left: 5%;
		        margin-top: 20px;
				.el-input {
					width: auto;
				}
				.el-textarea {
					width: 202px;
				}
			}
			.nav-class-btn {
				margin-top: 20px;
        		font-size: 15px;
		        padding-left: 5%;
		        margin-top: 20px;
			}
			.title {
				margin-right: 15px;
			}
		}
</style>


