<!--  -->
<template>
	<div class="right-content draw">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="" name="first">
				<el-table :data="drawList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="" prop="award_name"></el-table-column>
					<el-table-column label="" prop="award_total"></el-table-column>
					<el-table-column label="" prop="create_time"></el-table-column>
				 	<el-table-column label="" width="130">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="editDraw(scope.row)"></el-button>
	    					<el-button type="text" size="small" @click="deleteDraw(scope.row)"></el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
			</el-tab-pane>

			<el-tab-pane label="" name="second">
				<drawEditAndAdd v-if="add" v-on:change="activeChange($event)"></drawEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="" name="third" v-if="edit">
				<drawEditAndAdd v-on:change="activeChange($event)"></drawEditAndAdd>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'
	import drawEditAndAdd from '@/views/draw/draw-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			drawEditAndAdd
		},
		data() {
			return {
				loading: true,
				activeName: 'first',
				drawList: [],
				add: false,
				edit: false,
			}
		},
		methods: {
			// 
			changeTab(tab, event) {
				tab.index == 1 ? (this.add = true) | (this.$root.propType = 'add') : (this.add = false) | (this.edit = false);
			},
		    //   
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 
			editDraw(row) {
				this.$root.drawList = row;
				this.$root.propType = 'edit';
				this.edit = true;
				this.activeName = 'third';
			},
			// 
			deleteDraw(row) {
				apis.drawDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			          		if(bck.info == '') {
				            	location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			}
		},
		created() {
			apis.drawList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.drawList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.draw {
		
	}
</style>


