<!--  -->
<template>
	<div class="form-edit">
		<div class="add-form">
			<span class="title"></span>
			<el-input v-model="FormName" placeholder=""></el-input>
		  	<span class="required">*</span>
		</div>
		<div class="add-form" style="margin-top: 30px;">
			<span class="title"></span>
		</div>
		<div class="add-form" style="width: 75%;">
			<el-table :data="formListLength" style="width: 100%" :border='true'>
				<el-table-column label="">
					<template slot-scope="scope">
    					<el-input v-model="formListFields[scope.$index].title"></el-input>
  					</template>
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
    					<el-select v-model="formListFields[scope.$index].type" placeholder="" @change="selectOption">
						    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						    </el-option>
					  	</el-select>
					  	<el-button type="primary" v-if="formListFields[scope.$index].checkboxOrRadio" @click="setCheckboxData(scope.$index)" class="set-btn">{{formListFields[scope.$index].dataTitle != '' ? formListFields[scope.$index].dataTitle : ''}}</el-button>
  					</template>
				</el-table-column>
				<el-table-column label="" width="100">
					<template slot-scope="scope">
    					<el-checkbox v-model="formListFields[scope.$index].isMust"></el-checkbox>
  					</template>
				</el-table-column>
				<el-table-column label="" width="250">
      				<template slot-scope="scope">
      					<!--  -->
        				<el-button v-if="scope.$index != 0" @click="moveUp(scope.row)">
        					<i class="fa fa-list"></i>
        					<i class="fa fa-long-arrow-up"></i>
        				</el-button>
        				<!--  -->
        				<el-button v-if="scope.$index == 0 || scope.$index != formListLength.length - 1" @click="moveDown(scope.row)">
        					<i class="fa fa-list"></i>
        					<i class="fa fa-long-arrow-down"></i>
        				</el-button>
        				<!--  -->
    					<el-button icon="el-icon-delete" @click="deleteLine(scope.$index)"></el-button>
      				</template>
    			</el-table-column>
			</el-table>
		</div>
		<div class="add-form">
			<el-button type="primary" plain @click="addLine()"></el-button>
		</div>
		<div class="add-form" style="margin-top: 15px;">
			<el-button type="primary" @click="addOrEdit()"></el-button>
			<el-button @click="goBack()"></el-button>
		</div>


		<!-- / -->
		<el-dialog title="" :visible.sync="setDataDialog" class="set-data-dialog" width="40%" v-if="!reset">
	  		<el-table :data="formOptionList" style="width: 100%" :border='true' v-loading="loading">
	  			<el-table-column width="130">
					<template slot-scope="scope">
						<div @click="dataTitle = formOptionList[scope.$index].option_title">
        					<el-radio v-model="formListFields[inx].radio" :label="formOptionList[scope.$index].id">&nbsp;</el-radio>
						</div>
  					</template>
				</el-table-column>
				<el-table-column label="ID" prop="id" width="80"></el-table-column>
				<el-table-column label="" prop="option_title"></el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
      					<a href="javascript:;" @click="checkOption(scope.row)"></a>
      				</template>
				</el-table-column>
				<el-table-column label="" width="80">
      				<template slot-scope="scope">
    					<a href="javascript:;" @click="editFormOption(scope.row)">
      						<span class="operation-item" title=""><i class="fa fa-edit"></i></span>
      					</a>
      					<a href="javascript:;" @click="deleteFormOption(scope.row)">
      						<span class="operation-item" title=""><i class="fa fa-trash-o"></i></span>
      					</a>
      				</template>
    			</el-table-column>
	  		</el-table>
	  		<span slot="footer" class="dialog-footer">
		    	<el-button @click="newOption()"></el-button>
			    <el-button type="primary" @click="submitFormOptionId()"></el-button>
			</span>
		</el-dialog>
		

		<!--  -->
		<el-dialog title="" :visible.sync="checkOptionDialog" class="edit-data-dialog" width="40%">
	  		<el-table :data="optionList" style="width: 100%" :border='true'>
				<el-table-column label="" prop="id" width="120"></el-table-column>
				<el-table-column label="" prop="name"></el-table-column>
	  		</el-table>
	  		<span slot="footer" class="dialog-footer">
		    	<el-button @click="goBackList()"></el-button>
			</span>
		</el-dialog>


		<!--  -->
		<el-dialog title="" :visible.sync="editDataDialog" class="edit-data-dialog" width="40%">
			<div>
				<span class="title"></span>
				<el-input v-model="formOptionListName" placeholder="" style="margin-bottom: 15px;"></el-input>
				<span class="required">*</span>
			</div>

	  		<el-table :data="formOptionLength" style="width: 100%" :border='true' v-loading="loading">
				<el-table-column label="" width="120">
					<template slot-scope="scope">
      					<span>{{scope.$index + 1}}</span>
      				</template>
				</el-table-column>
				<el-table-column label="">
					<template slot-scope="scope">
      					<el-input v-model="formOptionTitle[scope.$index]"></el-input>
      				</template>
				</el-table-column>
				<el-table-column label="" width="80">
      				<template slot-scope="scope">
      					<a href="javascript:;" @click="deleteOneOption(scope.$index)">
      						<span class="operation-item" title=""><i class="fa fa-trash-o"></i></span>
      					</a>
      				</template>
    			</el-table-column>
	  		</el-table>
	  		<span slot="footer" class="dialog-footer">
		    	<el-button @click="goBackList()"></el-button>
		    	<el-button type="success" @click="addOneOption()"></el-button>
			    <el-button type="primary" @click="submitFormOptionAdd()"></el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				loading: true,
				setDataDialog: false,
				editDataDialog: false,
				checkOptionDialog: false,
				type: '',
				FormName: '',
				formListLength: [0, 1],
				formListFields: [
					{'title': '', 'type': 1, 'isMust': true, 'checkboxOrRadio': false, 'dataTitle': '', 'radio': ''},
					{'title': '', 'type': 2, 'isMust': true, 'checkboxOrRadio': false, 'dataTitle': '', 'radio': ''}
				],
				dataTitle: '',
				inx: Number,
				formOptionList: [],
				formOptionListId: '',
				formOptionListId2: '',
				options: [
					{label: '', value: 1},
					{label: '', value: 2},
					{label: '', value: 3},
					{label: '', value: 4},
					{label: '', value: 5},
					{label: '', value: 6},
					{label: '', value: 7},
					{label: '', value: 8},
					{label: '', value: 9},
					{label: '', value: 10},
					{label: '', value: 11},
					{label: '', value: 12}
				],
				formOptionId: Number,
				formOptionLength: [0, 1],
				formOptionListName: '',
				formOptionTitle: ['', ''],
				addFormOption: true,
				optionList: [],
				formList: [],
				formId: Number,
				reset: false,
			}
		},
		methods: {
            // 
            addLine() {
            	var inx = this.formListLength.length;

				this.formListLength.push(inx);
				this.formListFields.push({'title': '', 'type': 8, 'isMust': false, 'checkboxOrRadio': false, 'dataTitle': '', 'radio': ''});
            },
            // 
            deleteLine(row) {
            	this.formListLength.splice(row, 1);
            	this.reset = true;
				this.formListFields.splice(row, 1);
            },
            // 
            moveUp(row) {
            	var prev = row - 1;
            	
			   	this.formListFields.splice(prev, 0, (this.formListFields[row]));
			   	this.formListFields.splice(row + 1, 1);
            },
            // 
			moveDown(row) {
				var next = row + 2;

			   	this.formListFields.splice(next, 0, (this.formListFields[row]));
			   	this.formListFields.splice(row, 1);
			},
			// 
			selectOption(val) {
				var inx = '';

				for(var i = 0; i < this.formListFields.length; i++) {
					this.formListFields[i].type == val ? inx = i : '';
				}
				val == 11 || val == 12 ? this.formListFields[inx].checkboxOrRadio = true : this.formListFields[inx].checkboxOrRadio = false;
			},
			// 
			setCheckboxData(inx) {
				this.reset = false;
				this.type = this.formListFields[inx].type;
				this.inx = inx;
				this.setDataDialog = true;
				this.loading = true;

				apis.formOptionList({type: this.type, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					this.formOptionList = data;

					if(data.info == '') {
	            		this.$options.methods.alertMsg(this, data.info);
	            	}
				}.bind(this))
			},
			// 
			newOption() {
				this.editDataDialog = true;
				this.addFormOption = true;

				self.formOptionLength = [0, 1];
				self.formOptionListName = '';
				self.formOptionTitle = ['', ''];
			},
			// 
			addOneOption() {
				this.formOptionLength.push(this.formOptionLength.length);
				this.formOptionTitle.push('');
			},
			// 
			deleteOneOption(row) {
				this.formOptionLength.splice(row, 1);
				this.formOptionTitle.splice(row, 1);
			},
			// 
			goBackList() {
				this.checkOptionDialog = false;
				this.editDataDialog = false;
				this.formOptionLength = [0, 1];
				this.formOptionListName = '';
				this.formOptionTitle = ['', ''];
			},
			// 
			submitFormOptionAdd() {
				var self = this;
				var content = new Array();
				for(var i = 0; i < self.formOptionLength.length; i++) {
					content.push({'id': i + 1, 'name': self.formOptionTitle[i]});
				}
				
				if(this.addFormOption) {
					// 
					apis.formOptionAdd({option_title: self.formOptionListName, option_content: content, token: this.$root.token}, function(bck) {
						self.$alert(bck.info);
						if(bck.code == 1) {
							self.editDataDialog = false;
							self.loading = true;
							apis.formOptionList({type: self.type}, function(data) {
								data ? self.loading = false : '';
								self.formOptionList = data;
							}.bind(this))
						}
						else if(bck.info == '') {
		            		self.$options.methods.alertMsg(self, bck.info);
		            	}
					}.bind(this))
				}
				else {
					// 
					apis.formOptionEdit({id: self.formOptionId, option_title: self.formOptionListName, option_content: content, token: this.$root.token}, function(bck) {
						self.$alert(bck.info);
						if(bck.code == 1) {
							self.editDataDialog = false;
							self.loading = true;
							apis.formOptionList({type: self.type}, function(data) {
								data ? self.loading = false : '';
								self.formOptionList = data;
							}.bind(this))
						}
						else if(bck.info == '') {
		            		self.$options.methods.alertMsg(self, bck.info);
		            	}
					}.bind(this))
				}
			},
			// 
			deleteFormOption(row) {
				var self = this;
				apis.formOptionDelete({id: row.id, token: this.$root.token}, function(bck) {
					if(bck.code == 1) {
						apis.formOptionList({type: self.type, token: this.$root.token}, function(data) {
							data ? self.loading = false : '';
							self.formOptionList = data;
						}.bind(this))
					}
					else if(bck.info == '') {
						self.$options.methods.alertMsg(this, bck.info);
					}
				}.bind(this))
			},
			// 
			editFormOption(row) {
				this.editDataDialog = true;
				this.addFormOption = false;

				this.formOptionId = row.id;
				this.formOptionLength = [];
				this.formOptionTitle = ['', ''];
				this.formOptionListName = row.option_title;

				for(var i = 0; i < row.option_content.length; i++) {
					this.formOptionLength.push(i);
					this.formOptionTitle[i] = row.option_content[i].name;
				}
			},
			// 
			submitFormOptionId() {
				this.setDataDialog = false;
				this.formListFields[this.inx].dataTitle = this.dataTitle;
			},
			//  or 
			addOrEdit() {
				if(this.$root.propType == 'add') {
					apis.formAdd({form_name: this.FormName, content: this.formListFields, token: this.$root.token}, function(bck) {
						alertMassage(this, bck.info, bck.code);
					}.bind(this))
				}
				else {
					apis.formEdit({id: this.formId, form_name: this.FormName, content: this.formListFields, token: this.$root.token}, function(bck) {
						alertMassage(this, bck.info, bck.code);
					}.bind(this))
				}

				function alertMassage(_this, info, code) {
					_this.$alert(info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			            	if(code != 0) {
			            		_this.reload();
			            	}
			            	else if(info == '') {
			            		location.reload();
			            	}
			          	}
			        });
				}
			},
			// 
			checkOption(row) {
				this.checkOptionDialog = true;
				this.optionList = row.option_content;
			},
			// 
			goBack() {
				this.$emit("change", "first");
			},
			// 
			alertMsg: function(_this, info) {
            	this.$alert(info, {
	         	 	confirmButtonText: '',
		          	callback: action => {
		            		location.reload();
		            	}
		          	}
		        )
            },
		},
		created() {
			// 
			if(this.$root.propType == 'add') {

			}
			else {
				this.formList = this.$root.editForm;
				this.FormName = this.formList.form_name;
				this.formId = this.formList.id;
				this.formListFields = [];
				this.formListLength = [];

				for(var i = 0; i < this.formList.content.length; i++) {
					this.formListLength.push(i);
					this.formListFields.push({'title': this.formList.content[i].title, 'type': this.formList.content[i].type, 'isMust': this.formList.content[i].isMust, 'checkboxOrRadio': this.formList.content[i].checkboxOrRadio, 'dataTitle': this.formList.content[i].dataTitle, 'radio': this.formList.content[i].radio});
				}
			}
		}
	}
</script>

<style lang="less">
	.form-edit {
		.add-form, .edit-form {
		    font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		    .el-input, .el-textarea {
		    	width: auto;
		    }
		    .cell {
		    	text-align: center;
		    	.el-input {
		    		width: 100%;
		    	}
		    	.el-select {
		    		width: auto;
		    	}
		    }
		    .fa {
		    	color: #999;
		    }
		    .set-btn {
		    	margin-left: 15px;
		    }
		}
		.set-data-dialog, .edit-data-dialog {
			.el-dialog__footer, .cell {
				text-align: center;
			}
			.fa-trash-o {
				margin-left: 10px;
			}
		}
		.edit-data-dialog {
			.el-input {
		    	width: auto;
		    }
		    .title {
		    	margin-right: 15px;
		    }
		}
		a {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
	}
</style>


