<!--  -->
<template>
	<div class="right-content user-management">
		<div class="content">
			<el-table :data="userList" style="width: 100%" :border='true' v-loading="loading">
				<el-table-column label="ID" prop="id" width="80"></el-table-column>
				<el-table-column label="" prop="user_login"></el-table-column>
			 	<el-table-column label="" prop="user_nickname"></el-table-column>
			 	<el-table-column label="" width="80">
					<template slot-scope="scope">
						<img :src="url + userList[scope.$index].avatar" alt="">
					</template>
				</el-table-column>
			 	<el-table-column label="" prop="last_login_time"></el-table-column>
			 	<el-table-column label="IP" prop="last_login_ip"></el-table-column>
				<el-table-column label="" width="80">
					<template slot-scope="scope">
						<div>{{userList[scope.$index].user_status == 1 ? '' : ''}}</div>
					</template>
				</el-table-column>
			 	<el-table-column label="" width="80">
	  				<template slot-scope="scope">
						<el-button type="text" size="small" @click="changeStatus(scope.row)">{{userList[scope.$index].user_status == 1 ? '' : ''}}</el-button>
	  				</template>
				</el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				loading: true,
				userList: [],
				url: 'http://admin.dedaokq.com/',
			}
		},
		methods: {
			// 
			changeStatus(row) {
				row.user_status == 1 ? row.user_status = 0 : row.user_status = 1;
				apis.userBan({id: row.id, open: row.user_status, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
							if(bck.code == 0) {
				            	location.reload();
							}
			          	}
			        });
				}.bind(this))
			}
		},
		created() {
			apis.userList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.userList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.user-management {
		padding-top: 15px;
		img {
		    width: 55%;
		}
	}
</style>


