<!--  -->
<template>
	<div class="article-edit">
		<div class="flex">
			<div style="padding-left: 5%;width: 440px;">
				<div class="add-article-select">
					<span class="title"></span>
					<el-date-picker v-model="date" type="datetime" placeholder="" align="center"></el-date-picker>
					<span class="required">*</span>
				</div>
				<div class="add-article-select">
					<span class="title"></span>
					<el-cascader :options="articleList" v-model="selectedOptions" change-on-select></el-cascader>
					<span class="required">*</span>
				</div>
				<div class="add-article">
					<span class="title"></span>
					<el-input placeholder="" v-model="articleName" clearable></el-input>
					<span class="required">*</span>
				</div>
				<div class="add-article">
					<span class="title"></span>
					<el-input placeholder="" v-model="articleTag" clearable></el-input>
				</div>
				<div class="add-article">
					<span class="title" style="vertical-align: top;"></span>
					<el-input type="textarea" placeholder="" v-model="articleExcerpt" clearable></el-input>
				</div>
				<div class="add-article">
					<span class="title"></span>
					<el-radio v-model="radio" :label="true"></el-radio>
					<el-radio v-model="radio" :label="false"></el-radio>
				</div>
			</div>
			<div style="width: 500px;">
				<div class="img-upload flex">
					<div style="margin-right: 15px;"><span class="title"></span></div>
					<div class="preview-img" v-if="base64 != ''">
						<img :src="base64" class="slide-add-title">
						<el-button @click="destoryFile()" v-if="base64 != ''" style="margin-top: 15px;"></el-button>
					</div>
					<div>
						<div class="el-upload el-upload--picture-card" v-if="base64 == ''">
							<i class="el-icon-plus"></i>
							<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif">
						</div>
					</div>
				</div>
			</div>
		</div>
		<div style="padding-left: 5%;">
			<el-collapse-transition>
				<div v-show="radio" class="flex" style="flex-wrap: wrap;">
					<div class="add-article">
						<span class="title"></span>
						<el-input placeholder="" v-model="caseName" clearable></el-input>
					</div>
					<div class="add-article">
						<span class="title"></span>
						<el-input placeholder="" v-model="caseAge" clearable></el-input>
					</div>
					<div class="add-article">
						<span class="title"></span>
						<el-input placeholder="" v-model="caseSymptom" clearable></el-input>
					</div>
					<div class="add-article">
						<span class="title"></span>
						<el-input placeholder="" v-model="casePlan" clearable></el-input>
					</div>
				</div>
			</el-collapse-transition>
		</div>
		<div class="add-article" style="padding-left: 5%;">
			<span class="title" style="vertical-align: top;"></span>
			<div id="editorElem" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="add-article" style="padding-left: 5%;">
			<el-button type="primary" @click="addOrEdit()">{{type == 'add' ? '' : ''}}</el-button>
			<el-button @click="goBack()"></el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
	import E from 'wangeditor'
	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				articleList: [],
				selectedOptions: [],
				articleName: '',
				articleTag: '',
				articleExcerpt: '',
				editorContent: '',
				date: '',
				base64: '',
				parentId: [],
				articleId: 0,
				radio: false,
				caseName: '',
				caseAge: '',
				caseSymptom: '',
				casePlan: '',
				type: '',
			}
		},
		methods: {
			//  & 
			addOrEdit() {
				var more = null;
				if(this.type == 'add') {
					if(this.radio) {
						if(this.caseName == '') {
							this.$alert('');
						} else if(Math.floor(this.caseAge) != this.caseAge) {
							this.$alert('');
						} else {
							more = {
								'name': this.caseName,
								'age': this.caseAge,
								'symptom': this.caseSymptom,
								'plan': this.casePlan
							};
							apis.articleAdd({
								post_title: this.articleName,
								post_tag: this.articleTag,
								post_excerpt: this.articleExcerpt,
								post_content: this.editorContent,
								category_id: this.selectedOptions[this.selectedOptions.length - 1],
								published_time: this.date,
								thumb: this.base64,
								more: more,
								token: this.$root.token
							}, function(bck) {
								this.$alert(bck.info, {
									confirmButtonText: '',
									callback: action => {
										if(bck.info == '') {
											location.reload();
										} else if(bck.code == 1) {
											this.reload();
										}
									}
								});
							}.bind(this))
						}
					}else{
						apis.articleAdd({
							post_title: this.articleName,
							post_tag: this.articleTag,
							post_excerpt: this.articleExcerpt,
							post_content: this.editorContent,
							category_id: this.selectedOptions[this.selectedOptions.length - 1],
							published_time: this.date,
							thumb: this.base64,
							more: more,
							token: this.$root.token
						}, function(bck) {
							this.$alert(bck.info, {
								confirmButtonText: '',
								callback: action => {
									if(bck.info == '') {
										location.reload();
									} else if(bck.code == 1) {
										this.reload();
									}
								}
							});
						}.bind(this))
					}
				} else if(this.type == 'edit') {
					apis.articleEdit({
						id: this.articleId,
						post_title: this.articleName,
						category_id: this.selectedOptions[this.selectedOptions.length - 1],
						post_tag: this.articleTag,
						post_excerpt: this.articleExcerpt,
						post_content: this.editorContent,
						published_time: this.date,
						thumb: this.base64,
						more: more,
						token: this.$root.token
					}, function(bck) {
						this.$alert(bck.info, {
							confirmButtonText: '',
							callback: action => {
								if(bck.info == '') {
									location.reload();
								} else if(bck.code == 1) {
									this.reload();
								}
							}
						});
					}.bind(this))
				}
			},
			// 
			fileImage: function(e) {
				var self = this;
				var file = e.target.files[0];
				var reader = new FileReader();
				reader.readAsDataURL(file); // base64
				reader.onloadend = function() {
					//  base64 
					self.base64 = reader.result;
				};
			},
			// 
			destoryFile() {
				this.base64 = '';
			},
			// 
			goBack() {
				this.$emit("change", "first");
			}
		},
		created() {
			this.articleList = this.$root.propList;
			this.type = this.$root.propType;
			if(this.type == 'add') {
				this.date = new Date();
			} else if(this.type == 'edit') {
				var editArticle = this.$root.editArticle;
				this.date = editArticle.published_time;
				this.articleName = editArticle.post_title;
				this.parentId = editArticle.parents;
				this.parentId = this.parentId.split(',');
				this.selectedOptions = this.parentId.map(Number);
				this.editorContent = editArticle.post_content;
				this.articleId = editArticle.id;
				this.articleTag = editArticle.post_tag;
				this.articleExcerpt = editArticle.post_excerpt;
				if(editArticle.thumb) {
					this.base64 = editArticle.thumb;
				}
				if(editArticle.more === null) {
					this.radio = false;
				} else {
					this.radio = true;
					this.caseName = JSON.parse(editArticle.more).name;
					this.caseAge = JSON.parse(editArticle.more).age;
					this.caseSymptom = JSON.parse(editArticle.more).symptom;
					this.casePlan = JSON.parse(editArticle.more).plan;
				}
			}
		},
		mounted() {
			// 
			var editor = new E('#editorElem');
			var self = this;
			editor.customConfig.uploadImgShowBase64 = true; //  base64 
			// 
			editor.customConfig.colors = [
				'#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
			]
			// 
			editor.customConfig.menus = [
				'head', // 
				'bold', // 
				'fontSize', // 
				'fontName', // 
				'italic', // 
				'underline', // 
				'strikeThrough', // 
				'foreColor', // 
				'backColor', // 
				'link', // 
				'list', // 
				'justify', // 
				'quote', // 
				'image', // 
				'table', // 
				'video', // 
				'undo', // 
			]
			editor.customConfig.onchange = (html) => {
				this.editorContent = html;
			}
			editor.customConfig.debug = true;
			editor.create();
			editor.txt.html(this.editorContent);
		}
	}
</script>

<style lang="less">
	.article-edit {
		.el-radio__label {
			vertical-align: middle;
		}
		.img-upload {
			margin-top: 20px;
			.preview-img {
				width: 80%;
				img {
					width: 100%;
				}
			}
			.el-upload {
				position: relative;
				line-height: 148px;
			}
			.el-upload input {
				position: absolute;
				right: 0;
				top: 0;
				opacity: 0;
				cursor: pointer;
				width: 100%;
				height: 100%;
			}
		}
		.add-article,
		.add-article-select {
			margin-top: 20px;
			.title {
				margin-right: 15px;
				width: 75px;
				text-align: right;
				display: inline-block;
			}
			.el-input,
			.el-textarea {
				width: 217px;
			}
			.editorElem {
				width: 75%;
				display: inline-block;
			}
		}
	}
	
	.w-e-text-container {
		z-index: 0 !important;
	}
	
	.el-date-picker {
		z-index: 20000 !important;
	}
	
	.el-message-box__wrapper {
		z-index: 20009 !important;
	}
</style>


