<!--  -->
<template>
	<div class="right-content article-category">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">

			<el-tab-pane label="" name="first">

				<!--  -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order"></el-button>

				<el-table :data="articleList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="listOrder[scope.$index]" :placeholder="articleList[scope.$index].list_order" type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="">
	    				<template slot-scope="scope">
      						<div v-if="articleList[scope.$index]._level == 1">{{articleList[scope.$index].name}}</div>
      						<div v-else-if="articleList[scope.$index]._level == 2"><span style="margin: 0 15px 0 30px;"></span>{{articleList[scope.$index].name}}</div>
      						<div v-else-if="articleList[scope.$index]._level == 3"><span style="margin: 0 15px 0 60px;"></span>{{articleList[scope.$index].name}}</div>
      						<div v-else-if="articleList[scope.$index]._level == 4"><span style="margin: 0 15px 0 90px;"></span>{{articleList[scope.$index].name}}</div>
      					</template>
	    			</el-table-column>
				 	<el-table-column label="" prop="post_count"></el-table-column>
				 	<el-table-column label="" width="80">
				 		<template slot-scope="scope">
				 			<el-button type="text" size="small" @click="changeStatus(scope.row)">{{articleList[scope.$index].status == 1 ? '' : ''}}</el-button>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="" width="160">
	      				<template slot-scope="scope">
      						<el-button type="text" size="small" @click="control(scope.row, 'add')"></el-button>
	        				<el-button type="text" size="small" @click="control(scope.row, 'edit')"></el-button>
	    					<el-button type="text" size="small" @click="deleteArticle(scope.row)"></el-button>
	      				</template>
	    			</el-table-column>
	  			</el-table>
		    </el-tab-pane>

		    <el-tab-pane label="" name="second">
		    	<div class="add-article-select">
					<span class="title"></span>
					<el-cascader :options="articleList2" v-model="selectedOptions" change-on-select></el-cascader>
					<span class="required">*</span>
		    	</div>
		    	<div class="add-article">
		    		<span class="title"></span>
		    		<el-input placeholder="" v-model="addArticleName" clearable></el-input>
		    		<span class="required">*</span>
		    	</div>
		    	<div class="add-article">
		    		<span class="title" style="vertical-align: top;"></span>
		    		<el-input v-model="addArticleDescription" clearable type="textarea"></el-input>
		    	</div>
		    	<div class="add-article">
		    		<span class="title">SEO</span>
		    		<el-input placeholder="SEO" v-model="addSeoTitle" clearable></el-input>
		    	</div>
		    	<div class="add-article">
		    		<span class="title">SEO</span>
		    		<el-input placeholder="" v-model="addSeoKeyword" clearable></el-input>
		    	</div>
		    	<div class="add-article">
		    		<span class="title" style="vertical-align: top;">SEO</span>
		    		<el-input v-model="addSeoDescription" clearable type="textarea"></el-input>
		    	</div>
		    	<div class="add-article-select">
		    		<span class="title"></span>
					<el-select v-model="listTemplate" placeholder="">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
		    	<div class="add-article-select">
		    		<span class="title"></span>
					<el-select v-model="articleTemplate" placeholder="">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
		    	<div class="add-article">
		    		<el-button type="primary" @click="addArticle()"></el-button>
		    		<el-button @click="goBack()"></el-button>
		    	</div>
		    </el-tab-pane>
		</el-tabs>
		
		<!--  &  -->
		<el-dialog :title="type == 'add' ? '' : ''" :visible.sync="dialogVisible" class="add-dialog" width="40%">
			<div style="padding-left: 10%;">
	  			<p v-if="type == 'add'"><span class="title"></span><b>{{articleParent}}</b></p>
	  			<p v-else>
	  				<span class="title"></span>
					<el-cascader :options="articleList2" v-model="selectedOptions" change-on-select></el-cascader>
					<span class="required">*</span>
	  			</p>
				<p>
		    		<span class="title"></span>
		    		<el-input placeholder="" v-model="articleName" clearable></el-input>
		    		<span class="required">*</span>
		    	</p>
		    	<p>
		    		<span class="title" style="vertical-align: top;"></span>
		    		<el-input v-model="articleDescription" clearable type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
		    	</p>
		    	<p>
		    		<span class="title">SEO</span>
		    		<el-input placeholder="SEO" v-model="seoTitle" clearable></el-input>
		    	</p>
		    	<p>
		    		<span style="margin-right: 1px;">SEO</span>
		    		<el-input placeholder="" v-model="seoKeyword" clearable></el-input>
		    	</p>
		    	<p>
		    		<span class="title" style="vertical-align: top;">SEO</span>
		    		<el-input v-model="seoDescription" clearable type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
		    	</p>
		    	<p>
		    		<span class="title"></span>
					<el-select v-model="listTemplate" placeholder="">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</p>
		    	<p>
		    		<span class="title"></span>
					<el-select v-model="articleTemplate" placeholder="">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</p>
	  		</div>
	  		<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogVisible = false;"></el-button>
			    <el-button type="primary" @click="submitData()"></el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				articleList: [],
				articleList2: [],
				listOrder: [],
				loading: true,
				dialogVisible: false,
				selectedOptions: [],
				addArticleName: '',
				addArticleDescription: '',
				addSeoTitle: '',
				addSeoKeyword: '',
				addSeoDescription: '',
				articleParent: '',
				articleName: '',
				articleDescription: '',
				seoTitle: '',
				seoKeyword: '',
				seoDescription: '',
				id: 0,
				type: '',
				listOrder: [],
				cloneListOrder: [],
				idList: [],
				listOrderArr: [],
				originalListOrderArr: [],
				listTemplate: 0,
				articleTemplate: 0,
				listTPL: [],
			}
		},
		methods: {
			//
	      	changeTab(tab, event) {
				this.selectedOptions = [0];
				this.addArticleName = '';
				this.addArticleDescription = '';
				this.addSeoTitle = '';
				this.addSeoKeyword = '';
				this.addSeoDescription = '';
	      	},
	      	// 
	      	addArticle() {
	      		// methods
	      		this.$options.methods.submitAdd(this.addArticleName, this.selectedOptions[this.selectedOptions.length-1], this.addArticleDescription, this.addSeoTitle, this.addSeoKeyword, this.addSeoDescription, this, this.listTemplate, this.articleTemplate, this.$root.token);
	      	},
	      	// 
	      	goBack() {
	      		this.addArticleName = '';
				this.addArticleDescription = '';
				this.addSeoTitle = '';
				this.addSeoKeyword = '';
				this.addSeoDescription = '';
				this.activeName = 'first';
	      	},
	      	// 
	      	deleteArticle(row) {
	      		this.$confirm(''+ row.name +'', '', {
		          	distinguishCancelAndClose: true,
		          	confirmButtonText: '',
		          	cancelButtonText: ''
		        }).then(() => {
		            // 
		            apis.articleCategoryDelete({id: row.id, token: this.$root.token}, function(bck) {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				          		if(bck.info == ''){
				          			location.reload();
				          		}
				          		else {
				          			this.reload();
				            		this.reloadTab();
				          		}
				          	}
				        });
			    	}.bind(this))
	          	}).catch(action => {});
	      	},
	      	//  & 
	      	control(row, type) {
	      		this.type = type;
	      		this.dialogVisible = true;
      			this.id = row.id;
	      		if(type == 'add') {
	      			this.articleParent = row.name;
	      			this.articleName = '';
					this.articleDescription = '';
					this.seoTitle = '';
					this.seoKeyword = '';
					this.seoDescription = '';
	      		}
	      		else if(type == 'edit') {
                    if(row.parent_id==0){
                    	this.selectedOptions=[0];
                    }else{
                    	this.selectedOptions=[];
                    	let path=row.path.split(',');
                    	path.pop();
                    	for(var s=0; s<path.length;s++){ 			
                	 	this.selectedOptions.push(Number(path[s]));
                	}
                    }
	      			this.articleName = row.name;
	      			this.articleDescription = row.description;
	      			this.seoTitle = row.seo_title;
	      			this.seoKeyword = row.seo_keywords;
	      			this.seoDescription = row.seo_description;
	      			this.listTemplate = row.list_tpl;
					this.articleTemplate = row.one_tpl;
	      		}
	      	},
	      	// 
	      	submitData() {
	      		if(this.type == 'add') {
	      			// methods
	      			this.$options.methods.submitAdd(this.articleName, this.id, this.articleDescription, this.seoTitle, this.seoKeyword, this.seoDescription, this, this.listTemplate, this.articleTemplate);
	      		}
	      		else if(this.type == 'edit') {
	      			apis.articleCategoryEdit({id: this.id, name: this.articleName, parent_id: this.selectedOptions[this.selectedOptions.length-1], description: this.articleDescription, seo_title: this.seoTitle, seo_keywords: this.seoKeyword, seo_description: this.seoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
	      				bck.info == '' ? bck.info = '' : '';
	      				this.$alert(bck.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				          		if(bck.info == ''){
				          			location.reload();
				          		}
				          		else {
				          			this.reload();
				          		}
				          	}
				        });
	      			}.bind(this))
	      		}
	      	},
	      	// 
	      	submitAdd: function(name, id, description, seoTitle, seoKeywords, seoDescription, _this, listTemplate, articleTemplate, token) {
	      		apis.articleCategoryAdd({name: name, parent_id: id, description: description, seo_title: seoTitle, seo_keywords: seoKeywords, seo_description: seoDescription, list_tpl: listTemplate, one_tpl: articleTemplate, token:_this.$root.token}, function(bck) {
    				_this.$alert(bck.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			          		if(bck.info == ''){
			          			location.reload();
			          		}
			          		else if(bck.code == 1) {
			          			_this.reload();
			          		}
			          	}
			        });
	      		})
	      	},
	      	// 
	      	changeListOrder() {
	      		if(this.loading == true) {
	      			return;
	      		}
	      		var l_order = this.listOrder;
	    		var id = this.idList;
	    		
	    		for(var i = 0; i < id.length; i++) {
					this.listOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(l_order))[i]})
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]})
				}
				
				apis.articleCategoryListOrder({list_order: this.listOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
					bck.info == '' ? bck.info = '' : '';
					this.$alert(bck.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			          		if(bck.info == ''){
			          			location.reload();
			          		}
			          		else if(bck.code == 1) {
			          			this.reload();
			          		}
			          	}
			        });
				}.bind(this))
	      	},
	      	//  & 
	      	changeStatus(row) {
	      		var statu;
	      		row.status == 1 ? statu = 0 : statu = 1;
	      		apis.articleCategoryStatus({id: row.id, status: statu, token: this.$root.token}, function(bck) {
	      			bck.code == 1 ? row.status = statu : '';
	      			if(bck.info == '') {
	      				this.$alert(bck.info, {
			         	 	confirmButtonText: '',
				          	callback: action => {
				          		location.reload();
				          	}
				        });
	      			}
	      		}.bind(this))
	      	}
		},
		created() {
			apis.articleCategoryList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.articleList = data;
					// idlist-order
					for(var i = 0; i < data.length; i ++) {
		    			this.idList.push(data[i].id);
		    			this.listOrder.push(data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.listOrder);
				}
			}.bind(this))

			apis.articleCategoryListTree({token: this.$root.token}, function(data) {
				this.articleList2 = data;
    			this.articleList2.unshift({'value': 0, 'label': ''});
			}.bind(this))

			apis.themeList({token: this.$root.token}, function(data) {
				this.listTPL = data;
				this.listTPL.unshift({'id': 0, 'name': ''});
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.article-category {
		.change-list-order {
			margin-bottom: 15px;
		}
		.add-article-select, .add-article {
			font-size: 15px;
			padding-left: 5%;
			margin-top: 20px;
			.title {
				margin-right: 15px;
				width: 75px;
				text-align: right;
				display: inline-block;
			}
			.el-input, .el-textarea {
				width: 217px;
			}
		}
		.add-dialog {
			.title {
				margin-right: 15px;
			}
			.el-input, .el-textarea {
				width: 217px;
			}
		}
		.w-e-text-container {
			height: 350px !important;
			z-index: 999 !important;
		}
		.w-e-toolbar .w-e-menu {
			z-index: 1900 !important;
		}
	}
</style>


